'use strict';

$('#bannerCarousel').owlCarousel({
    loop: true,
    nav: false,
    items: 1,
});

$('#itemsCarousel').owlCarousel({
    loop: true,
    nav: true,
    dots: false,
    margin: 30,
    autoplay: true,
    autoplayTimeout: 5000,
    autoplayHoverPause: true,
    slideBy: 4,
    items: 4,
});

$('#productsCarousel').owlCarousel({
    loop: true,
    margin: 30,
    nav: false,
    responsive: {
        0:{
            items:1
        },
        440:{
            items: 2
        },
        700:{
            items: 3
        },
        1100:{
            items: 4,
        }
    }
})

const loginModal = document.querySelector('#loginModal'),
loginBtn = document.querySelector('#loginBtn'),
passBtn = document.querySelector('#passBtn'),
passField = document.querySelector('#passField');

const cartCounts = document.querySelector('#cartCounts');
const plusButton = document.querySelectorAll('.plus-button');

const wishCounts = document.querySelector('#wishCounts');
const heartButton = document.querySelectorAll('.heart-button');

const year = document.querySelector('#year');

// Open the modal 
loginBtn.addEventListener('click', () => {
    loginModal.style.display = 'block';
});
// Close the modal
document.addEventListener('click', (e) => {
    if (e.target === loginModal) {
        loginModal.style.display = 'none';
    }
});
document.addEventListener('keydown', (e) => {
    if(e.key === 'Escape') {
        loginModal.style.display = 'none';
    }
});
// Show password
function showPass() {
    if (passField.type === "password") {
        passField.type = "text";
    } else {
        passField.type = "password";
    }
}; 
passBtn.addEventListener('click', () => {
    showPass();
});


// Add goods to cart
let clicks = 0;

function initiateClicks() {
    let clickNum = localStorage.getItem('items');
    if(clickNum == undefined){
        localStorage.setItem('items', 0);
        clicks = 0;
    }else{
        clicks = parseInt(clickNum);   
    }
    cartCounts.innerHTML = clicks;
}

plusButton.forEach(function(item) {
    item.addEventListener('click', () => {
        clicks += 1;
        localStorage.setItem('items', clicks);
        cartCounts.innerHTML = clicks;
    });
});

initiateClicks();

// Add goods to wish-list
let hearts = 0;

function initiateHearts() {
    let heartNum = localStorage.getItem('hearts');
    if(heartNum == undefined){
        localStorage.setItem('hearts', 0);
        hearts = 0;
    }else{
        hearts = parseInt(heartNum);   
    }
    wishCounts.innerHTML = hearts;
}

heartButton.forEach(function(item) {
    item.addEventListener('click', () => {
        hearts += 1;
        localStorage.setItem('hearts', hearts);
        wishCounts.innerHTML = hearts;
    });
});

initiateHearts();

// Get year
function getYear() {
    let today = new Date();
    let yearAct= today.getFullYear();
    year.innerHTML = yearAct;
}
getYear();






